import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class TemplateService {

  constructor(private http: HttpClient) { }
    
  templates(): Promise<{total_count: number, items: Array<any>}> {
    return this.http
      .get<{total_count: number, items: Array<any>}>(`${environment.apiServer}/templates`)
      .toPromise();
  }
  subTemplates(page: number, perPage: number): Promise<{total_count: number, items: Array<any>}> {
    return this.http
      .get<{total_count: number, items: Array<any>}>(`${environment.apiServer}/templates?page=${page}&per_page=${perPage}`)
      .toPromise();
  }

//   getTemplateImageUrl(id: string, url: string): string {
    // return this.http
    //   .get<Array<any>>(`${environment.apiServer}/preview?id`,)
    //   .toPromise();
//   }
}
